<template>
 <div class="comments-slider-container">
  <div class="container">
    <h2 class="goods-slider__title comments-slider__title">
        {{ title }}
    </h2>
    <swiper
      :breakpoints="swiperOptions.breakpoints"
      :slides-per-view="1"
      :loop="true"
      :pagination="{ clickable: true }"
      class="goods-slider comments-slider"
    >
      <swiper-slide class="slide goods-slider__slide" v-for="(commt, index) in comments" :key="index">
          <comment :comm="commt" />
      </swiper-slide>
    </swiper>
  </div>  
 </div>   
</template>
<script>

  import comment from '@/components/comment';
  import SwiperCore, { Pagination, Autoplay } from 'swiper';

  import { Swiper, SwiperSlide } from 'swiper/vue';

  import 'swiper/swiper-bundle.css';

  SwiperCore.use([ Pagination, Autoplay]);

  export default {
    props: {
       comments: {
          type: Array,
          required: true
       },
       title: {
          type: String,
          default: 'Категория'
       }
    },
    components: {
      comment,
      Swiper,
      SwiperSlide,
    },
    data() {
        return {
            swiperOptions: {
                breakpoints: {
                    1200: {
                        slidesPerView: 3,
                        spaceBetween: 20
                    },
                    850: {
                        slidesPerView: 2,
                        spaceBetween: 20
                    },
                    770: {
                        slidesPerView: 1,
                        spaceBetween: 20
                    },
                    620: {
                        slidesPerView: 2,
                        spaceBetween: 10
                    }
                }
            }
        }
    }
  };
</script>