<template>
	<div class="container">
		<div class="contacts">
			<h3 class="contacts__title"> Информационная страница </h3>
			<div class="contacts__text"> Современные технологии достигли такого уровня, что социально-экономическое развитие обеспечивает актуальность переосмысления внешнеэкономических политик. Равным образом, семантический разбор внешних противодействий играет важную роль в формировании направлений прогрессивного развития. </div>
			<form action="#" class="contacts-form" @submit.prevent="checkValidate">
				<label for="name" class="contacts-label">
					<span class="contacts-span"> Имя * </span>
					<input type="text" id="name" :class="{error: nameValid }" class="contacts-input" v-model.trim="userContacts.name">
					<span class="error-span" v-if="nameValid"> Необходимо ввести имя </span>
				</label>
				<label for="phone" class="contacts-label">
					<span class="contacts-span"> Телефон * </span>
					<input type="tel" id="phone" :class="{error: phoneValid }" class="contacts-input" v-model.trim="userContacts.phone">
					<span class="error-span" v-if="phoneValid"> Введите корректный номер телефона </span>
				</label>
				<label for="email" class="contacts-label">
					<span class="contacts-span"> Email * </span>
					<input type="email" id="email" :class="{error: emailValid }" class="contacts-input" v-model.trim="userContacts.email">
					<span class="error-span" v-if="emailValid"> Введите корректный email </span>
				</label>
				<textarea name="msg" id="msg" :class="{error: msgValid }" placeholder="Текст сообщения*" class="contacts-textarea" v-model.trim="userContacts.msg"></textarea>
				<span class="error-span textarea-error" v-if="msgValid"> Поле не должно быть пустым </span>
				<input type="submit" class="contacts-submit" value="Отправить">
			</form>
		</div>
	</div>
</template>

<script>
	
	export default {
		name: 'Contacts',
		data() {
			return {
				nameValid: false,
				emailValid: false,
				phoneValid: false,
				msgValid: false,
				emailRegular: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
				phoneRegular: /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/,
				userContacts: {
					 name: '',
					 email: '',
					 phone: '',
					 msg: '',
				},
			}
		},
		methods: {
			checkValidate() {
				let newData = Object.entries(this.userContacts);
				return newData.forEach(item => {
						if (item[0] === 'name') {
							if (item[1] === '') {
								this.nameValid = true;
							} else {
								this.nameValid = false;
							}
							return item[1] !== '';
						}
						if (item[0] === 'phone') {
							if (!this.phoneRegular.test(item[1])) {
								this.phoneValid = true;
							} else {
								this.phoneValid = false;
							}
							return this.phoneRegular.test(item[1]);
						}
						if (item[0] === 'email') {
							if (!this.emailRegular.test(item[1])) {
								this.emailValid = true;
							} else {
								this.emailValid = false;
							}
							return this.emailRegular.test(item[1]);
						}
						if (item[0] === 'msg') {
							if (item[1] === '') {
								this.msgValid = true;
							} else {
								this.msgValid = false;
							}
							return item[1] !== '';
						}
				});
			}
		}
	};

</script>

