<template>
	<div class="comment">
		<div class="comment__header">
			<img :src="commentImage" alt="author" class="comment__image">
			<div class="comment__box">
				<p class="comment__header-name">
				{{ comm.author }}
				</p>
				<p class="comment__header-profession">
					{{ comm.profession }}
				</p>
			</div>
		</div>
		<div class="comment__body">
			<p class="comment__text">
				{{ comm.text }}
			</p>
		</div>
	</div>
</template>

<script>
	
	export default {
		name: 'comment',
		props: {
			comm: {
				type: Object,
				required: true
			}
		},
		data() {
			return {

			}
		},
		computed: {
			way() {
				return this.comm.source;
			},
			commentImage() {
				return require(`../assets/images/comments/${this.way}.png`);
			}
		},
		methods: {

		}
	};

</script>

