<template>
	<div class="cart-container">
		<div class="cart-item">
			<div class="cart-item__picture">
				<img :src="imageUrl" alt="image">
			</div>
			<div class="cart-item__inform">
				<p class="cart-item__name"> {{ item.name.split('(')[0] }} </p>
				<p class="cart-item__text">
					<span class="cart-item__span"> {{ item.description }} </span>
					<span class="cart-item__span">
						Цена: {{ item.price }}&#8381
					</span>
				</p>
			</div>
		</div>
		<div class="cart-item-buttons-box">
			<div class="cart-item-buttons">
				<button class="button-plus" @click="setCnt({ id: item.id, count: item.count + 1 })">+</button>
				<p class="cart-item-input"> {{ item.count }} </p>
				<button class="button-minus" @click="setCnt({ id: item.id, count: item.count - 1 })">-</button>
			</div>
			<button class="cart-remove-btn" @click="removeFromCart(this.item.id)">
				Убрать
			</button>
		</div>
	</div>
</template>

<script>

	import { mapGetters, mapActions } from 'vuex';
	
	export default {
		name: 'CartItem',
		props: {
			item: {
				type: Object,
				required: true,
				default: {
					name: 'prop',
				}
			}
		},
		computed: {
			imageUrl() {
				return require(`../assets/images/products/${this.item.image}.png`);
			},
			currentCount() {
				return this.item.count;
			}
		},
		methods: {
			...mapActions('cart', ['setCnt']),
			...mapActions('cart', ['removeFromCart']),
		},
	};

</script>