<template>
  <div class="container">
    <h2 class="goods-slider__title">
        {{ title }}
    </h2>
    <swiper
      :slides-per-view="1"
      :space-between="10"
      :loop="true"
      :breakpoints="swiperOptions.breakpoints"
      :pagination="{ clickable: true }"
      class="goods-slider"
    >
      <swiper-slide class="slide goods-slider__slide" v-for="(good, index) in goods" :key="index">
          <product-card :item="good" />
      </swiper-slide>
    </swiper>
  </div>  
</template>
<script>

  import ProductCard from '@/components/ProductCard';
  import SwiperCore, { Pagination, Autoplay } from 'swiper';

  import { Swiper, SwiperSlide } from 'swiper/vue';

  import 'swiper/swiper-bundle.css';

  SwiperCore.use([ Pagination, Autoplay]);

  export default {
    props: {
       goods: {
          type: Array,
          required: true
       },
       title: {
          type: String,
          default: 'Категория'
       }
    },
    components: {
      ProductCard,
      Swiper,
      SwiperSlide,
    },
    data() {
        return {
            swiperOptions: {
                breakpoints: {
                    1200: {
                        slidesPerView: 4,
                        spaceBetween: 20
                    },
                    900: {
                        slidesPerView: 3,
                        spaceBetween: 20
                    },
                    769: {
                        slidesPerView: 2,
                        spaceBetween: 20
                    },
                    670: {
                        slidesPerView: 4,
                        spaceBetween: 10
                    },
                    520: {
                        slidesPerView: 3,
                        spaceBetween: 10
                    },
                    360: {
                        slidesPerView: 2,
                        spaceBetween: 10
                    }
                }
            }
        }
    }
  };
</script>
