export default {
	namespaced: true,
	state: {
		cart: [],
	},
	getters: {
		length: state => state.cart.length,
		inCart: state => state.cart,
		hasItem: state => id => state.cart.some(item => item.id == id),
		total: state => state.cart.reduce((acc, item) => {
			return acc += item.sum;
		}, 0),
	},
	mutations: {
		add(state, item) {
			item.sum = item.price;
			state.cart.push(item);
			let thisCart = JSON.stringify(state.cart);
			localStorage.setItem('thisCart', thisCart);
			state.cart = JSON.parse(localStorage.getItem('thisCart'));
		},
		removeProduct(state, id) {
			state.cart = state.cart.filter(pr => pr.id !== id);
			let removedCart = JSON.stringify(state.cart);
			localStorage.setItem('thisCart', removedCart);
			state.cart = JSON.parse(localStorage.getItem('thisCart'));
		},
		setCount(state, {id, count}) {
			let i = state.cart.findIndex(pr => pr.id === id);
			let cnt = parseInt(count);
			state.cart[i].count = Math.max(1, cnt);
			state.cart[i].sum = state.cart[i].price * state.cart[i].count;
			let cart = JSON.stringify(state.cart);
			localStorage.setItem('thisCart', cart);
			state.cart = JSON.parse(localStorage.getItem('thisCart'));
		},
		load(state) {
			state.cart = JSON.parse(localStorage.getItem('thisCart'));
		}
	},
	actions: {
		addToCart(store, item) {
			store.commit('add', item);
		},
		removeFromCart(store, id) {
			store.commit('removeProduct', id);
		},
		setCnt(store, payload) {
			store.commit('setCount', payload);	
		},
		loadCart(store) {
			if (localStorage.getItem('thisCart')) {
				store.commit('load');
			}
		}
	}
}