import goods from '@/db.js';

export default {
	namespaced: true,
	state: {
		products: [],
	},
	getters: {
		all: state => state.products,
		hits: state => state.products[0],
		newItems: state => state.products[1],
		discounts: state => state.products[2],
		allProducts(state) {
			let allPr = [];
			state.products.forEach(pr => {
				pr.forEach(item => {
					allPr.push(item);
				})
			});
			return allPr;
		},
		currentProduct: (state, getters) => id => getters.allProducts.find(pr => pr.id == id),
	},
	mutations: {
		setProducts(state, products) {
			state.products = products;
		},
	},
	actions: {
		async load(store) {
			let newGoods = goods;
			store.commit('setProducts', newGoods);
		}
	}
}