<template>
	<div class="main">
		<main-slider />
		<goods-slider :goods="hits" :title="hitsTitle" />
		<goods-slider :goods="newItems" :title="newItemsTitle" />
		<goods-slider :goods="discounts" :title="discountsTitle" />
		<comments-slider :comments="comms" :title="commentsTitle" />
	</div>
</template>

<script>
	
	import { mapGetters } from 'vuex';
	import MainSlider from '@/components/MainSlider';
	import GoodsSlider from '@/components/GoodsSlider';
	import CommentsSlider from '@/components/CommentsSlider';

	import inform from '@/comments.js';
	
	export default {
		name: 'Main',
		data() {
			return {
				hitsTitle: 'Хиты продаж',
				newItemsTitle: 'Новинки',
				discountsTitle: 'Товары по акции',
				commentsTitle: 'отзывы наших клиентов',
				comms: inform,
			}
		},
		components: {
			MainSlider,
			GoodsSlider,
			CommentsSlider
		},
		computed: {
			...mapGetters('products', { products: 'all' }),
			...mapGetters('products', { newItems: 'newItems' }),
			...mapGetters('products', { hits: 'hits' }),
			...mapGetters('products', { discounts: 'discounts' }),
		},
	};

</script>

