<template>
	<div class="add-button">
		<button type="button" class="add-button__btn" @click="addNewItem" v-show="!this.hasCurrentItem">
			В корзину
		</button>
		<button type="button" class="add-button__btn add-button__btn--remove" @click="removeProduct" v-show="this.hasCurrentItem">
			Убрать из корзины
		</button>
	</div>
</template>

<script>

	import { mapGetters, mapActions } from 'vuex';
	
	export default {
		name: 'AddButton',
		props: {
			product: {
				type: Object,
				required: true
			}
		},
		data() {
			return {
				
			}
		},
		computed: {
			...mapGetters('cart', { length: 'length'}),
			...mapGetters('cart', { hasItem: 'hasItem'}),
			hasCurrentItem() {
				return this.hasItem(this.product.id);
			}
		},
		methods: {
			...mapActions('cart', { addToCart: 'addToCart' }),
			...mapActions('cart', { removeFromCart: 'removeFromCart' }),
			addNewItem() {
				let pr = this.product;
				if(!this.hasCurrentItem) {
					this.addToCart(pr);
				}
			},
			removeProduct() {
				this.removeFromCart(this.product.id);
			}
		}
	};

</script>

