<template>
	<div class="product-page" v-if="hasProduct">
		<div class="product-page-container">
			<h3 class="product-page__title">
				{{ currentProduct.name.split('(')[0] }}
			</h3>
			<div class="product-page-top">
				<div class="product-page-slider">
					<div class="product-page-big">
						<img :src="productPicture" alt="image" class="product-page-big__img" ref="bigImage">
					</div>
					<div class="product-page-pictures">
						<div class="product-page-picture" v-for="n in 3" :key="n">
							<img :src="productPicture" alt="image" class="product-page-picture__img" @click="changeImage">
						</div>
					</div>
				</div>
				<div class="product-page-inform">
					<p class="product-page-price">
						Цена: {{ currentProduct.price }}&#8381
					</p>
					<p class="variant-span">
						Выберите варианты:
					</p>
					<div class="product-page-variants">
						<button type="button" v-for="(taste, i) in currentProduct.tastes" :key="i" @click="changeTaste(taste)" class="product-page-btn">
							{{ taste }}
						</button>
					</div>
					<p class="product-page-text">
						{{ currentProduct.description.split('(')[0] }}
					</p>
					<add-button :product="currentProduct" />
				</div>
			</div>
			<div class="product-page-characteristics">
				<p class="product-characteristics-name">
					Характеристики
				</p>
				<div class="product-page-characteristics-box">
					<div class="product-characteristics-brend">
						<p class="product-characteristics-paragraph">
							Бренд: FITNESS FOOD FACTORY
						</p>
						<p class="product-characteristics-paragraph">
							Вкус: <span v-for="(taste, i) in currentProduct.tastes" :key="i" class="taste-span"> {{ taste }} </span>
						</p>
					</div>
					<div class="product-characteristics-inform">
						<ul class="product-characteristics-list">
							<li class="product-characteristics-list__item">
								 Высокое содержание белка и полиненасыщенных жиров.
							</li>
							<li class="product-characteristics-list__item">
								 Содержит фолиевую кислоту, цинк, магний, железо и минералы
							</li>
							<li class="product-characteristics-list__item">
								  Укрепляет иммунитет, нормализует кровяное давление и улучшает обменные процессы.
							</li>
						</ul>
						<p class="product-characteristics-inform__text"> {{ productTaste }} - 100% вкус натурального аргентинского арахиса среднего помола. Мягкая текстура и природная сладость без сахара и других добавок. </p>
					</div>
				</div>
			</div>
				<goods-slider :goods="discounts" :title="goodsTitle" class="goods-slider-container" />
		</div>
	</div>
	<app-e404 v-else title="Страница не найдена"></app-e404>
</template>

<script>

	import { mapGetters } from 'vuex';

	import GoodsSlider from '@/components/GoodsSlider';
	import AppE404 from '@/components/E404';
	import AddButton from '@/components/AddButton';
	
	export default {
		name: 'ProductPage',
		components: {
			GoodsSlider,
			AppE404,
			AddButton
		},
		data() {
			return {
				productTaste: '',
				goodsTitle: 'С этим товаром покупают'
			}
		},
		computed: {
			...mapGetters('products', { current: 'currentProduct' }),
			...mapGetters('products', { discounts: 'discounts' }),
			id() {
				return this.$route.params.id;
			},
			currentProduct() {
				return this.current(this.id);
			},
			productPicture() {
				return require(`../assets/images/products/${this.currentProduct.image}.png`);
			},
			hasProduct(){
				return this.currentProduct !== undefined;
			}
		},
		methods: {
			changeTaste(taste) {
				this.productTaste = taste;
			},
			changeImage(e) {
				let picture = e.target.src;
				let bigImage = this.$refs.bigImage;
				bigImage.src = picture;
			}
		},
		mounted() {
			if (this.hasProduct) {
				this.productTaste = this.currentProduct.tastes[0];
			}
		},
	};

</script>

