<template>
	<div class="product-card">
		<div class="product-card__picture">
			<router-link :to="`/page/${item.id}`"> 
				<img :src="productImage" alt="image" class="product-card__image">
			</router-link>
		</div>
		<h3 class="product-card__title">
			<router-link :to="`/page/${item.id}`" class="product-card__link"> {{ item.name }} </router-link>
		</h3>
		<p class="product-card__text">
			{{ item.description }}
		</p>
		<div class="product-card__stars" @click="onStars" ref="stars">
			<svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg" class="product-card__star">
            <path d="M10 15.27L16.18 19L14.54 11.97L20 7.24L12.81 6.63L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27Z"/>
            </svg>
            <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg" class="product-card__star">
            <path d="M10 15.27L16.18 19L14.54 11.97L20 7.24L12.81 6.63L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27Z"/>
            </svg>
            <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg" class="product-card__star">
            <path d="M10 15.27L16.18 19L14.54 11.97L20 7.24L12.81 6.63L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27Z"/>
            </svg>
            <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg" class="product-card__star">
            <path d="M10 15.27L16.18 19L14.54 11.97L20 7.24L12.81 6.63L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27Z"/>
            </svg>
            <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg" class="product-card__star">
            <path d="M10 15.27L16.18 19L14.54 11.97L20 7.24L12.81 6.63L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27Z"/>
            </svg>
		</div>
		<p class="product-card__price">
			{{ item.price }}&#8381
		</p>
		<div class="product-card__btn">
			<add-button :product="item" />
		</div>
	</div>
</template>

<script>

	import AddButton from '@/components/AddButton';
	
	export default {
		name: 'ProductCard',
		components: {
			AddButton
		},
		props: {
			item: {
				type: Object,
				required: true
			}
		},
		data() {
			return {

			}
		},
		computed: {
			starsCount() {
				return this.item.stars;
			},
			imgURL() {
				return this.item.image;
			},
			productImage() {
				return require(`../assets/images/products/${this.imgURL}.png`);
			}
		},
		mounted() {
			for (let i = 0; i < this.starsCount; i++) {
				let stars = this.$refs.stars.children;
				stars[i].style.fill = '#F6A207';
			}
		},
	};

</script>

